	module.exports = {"data":{"markdownRemark":{"html":"<p>Hi there. You may be finding yourself on this podcast thinking “is this some sort of questionable third rate movie podcast where two dudes sit in their mom's basements and talk about movies? What sort of insight could they possibly have on the movie Deadpool 2 that other better podcasts couldn’t have?” Let me say this: I hear you. The answer is that we’re narcissistic egomaniacs that think you should listen to this episode. But maybe that’s just enough? Probably not. But ask yourself this: if you’ve read this episode description this far haven’t you already put in enough investment to ride it out? Sure, it might not all pan out how you’ve hoped. But at this point you owe it to us and yourself to finish up here. Go ahead. Press play. Maybe you’ll like it. Probably not.</p>\n<!-- excerpt-end -->\n<h4>Show Notes:</h4>\n<ul>\n<li><a href=\"https://www.imdb.com/title/tt5463162/\">Deadpool</a></li>\n<li><a href=\"https://www.imdb.com/title/tt0458525/\">X-Men Origins: Wolverine</a></li>\n<li><a href=\"http://www.denofgeek.com/us/movies/deadpool/237794/deadpool-movie-test-footage-leaks-is-perfect\">Deadpool Test Footage</a></li>\n<li><a href=\"https://www.theverge.com/tldr/2018/5/18/17365998/deadpool-2-marketing-stunts-deadpool-x-david-beckham-forever\">Deadpool Marketing Roundup (TheVerge)</a></li>\n<li><a href=\"https://www.google.com/amp/s/www.progressiveboink.com/platform/amp/2012/4/21/2960508/worst-rob-liefeld-drawings\">Horrible Rob Liefeld Drawings</a></li>\n<li><a href=\"https://www.amazon.com/Spider-Man-Deadpool-Vol-Isnt-Bromantic/dp/0785197869\">Spiderman Deadpool Comic</a></li>\n</ul>","frontmatter":{"title":"Deadpool 2"}}},"pathContext":{"slug":"/episodes/015-Deadpool/"}}

